import { useNavigate, useParams} from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss'
import {useEffect, useState} from 'react';

let 박스 = styled.div`
    padding: 20px;
`;

let 제목 = styled.h4`
    font-size: 25px;
    color : ${props => props.책상}
`;

function Detail(props){
    let [showAlert , setShowAlert] = useState(true);
    let [inp, setInp] = useState('');

    useEffect(() => {
        let 타이머 = setTimeout(() => {
            setShowAlert(false)
        }, 2000)
        return () => {
            clearTimeout(타이머)
        }
    },[showAlert])

    let history = useNavigate();
    function handleClick(){
        history('/');
    }
    let {id} = useParams();
    let goods = props.shoes.find(function(good){
        return good.id.toString() === id
    })
    
    return(
      <div className="container">
        <박스>
            <제목 책상='red'>상세페이지</제목>
        </박스>

        <input onChange={(e) => {setInp(e.target.value)}}/>

        {showAlert ? 
        <div className='my-alert'>
            <p>재고가 얼마 남지 않았습니다.</p>
        </div>: null}
        <div className="row">
          <div className="col-md-6">
            <img src={`https://codingapple1.github.io/shop/shoes${Number(id) + 1}.jpg`} width="100%" />
          </div>
          <div className="col-md-6 mt-4">
            <h4 className="pt-5">{goods.title}</h4>
            <p>{goods.content}</p>
            <p>{goods.price}</p>


            <Info 재고={props.재고}></Info>

            <button className="btn btn-danger" onClick={()=> {props.재고변경([...props.재고])}}>주문하기</button>
            &nbsp;
            <button className="btn btn-danger" onClick={handleClick}>뒤로가기</button>
          </div>
        </div>
      </div> 
    )
  }


  function Info(props){
      return(
        <p>재고: {props.재고[0]}</p>
      )
  }

export default Detail;